<!-- 中奖提示传 -->
<template>
	
	<cover-view class="xh-winning-window xh-dialog" :style="{display: isShow?'block':'none'}">
		<cover-view class="xh-dialog-body xhDialog">
			     <!-- toast 背景 -->
				 <cover-image class="dialog-bg" src="../static/images/dialog_bg.png"></cover-image>
		         <!-- toast header --> 
				 <cover-image v-if="type == '25'" class="dialog-bg-header-25" src="../static/images/dialog_header_25.png"></cover-image>
				 <cover-image v-else class="dialog-bg-header-26" src="../static/images/dialog_header_26.png"></cover-image>
				 <!-- 恭喜获得 -->
				 <cover-image class="dialog-tips-img" src="../static/images/dialog_tips_img.png"></cover-image>
				 <!-- 主要内容 -->
				 <cover-view class="card-coupons">
					 <!-- 25or26周年 -->
					 <cover-image v-if="type == '25'" class="cc-left-icon" src="../static/images/mcb_no_converted25.png"></cover-image>
					 <cover-image v-else class="cc-left-icon" src="../static/images/mcb_no_converted26.png"></cover-image>
					 <!-- 右边背景 -->
					 <cover-image class="cc-right-bg" src="../static/images/mcb_bg_white.png"></cover-image>
					 <cover-view class="cc-right-info">
						 <cover-view class="cc-r-i-title">{{type == '25'?'1元轻松享兑换劵':'1元享红牛兑换券'}}</cover-view>
						 <cover-view class="cc-r-i-time">领取时间：2021-03-15 13:00:22</cover-view>
					 </cover-view>
				 </cover-view>
				 <!-- 按钮部分 -->
				  <cover-view class="xh-dialog-btn">
					  <cover-view class="xdb-item" @click="goCardBag">
					  	<cover-view class="xdb-item-text deposit">存入卡包</cover-view>
					  	<cover-image class="xdb-item-bg" src="../../../static/images/dialog_btn_bg02.png"></cover-image>
					  </cover-view>
					  <cover-view class="xdb-item">
						<button v-if="userInfo.mobile" class="xdb-item exchange" @click="exchange">马上兑奖</button>
						<button v-else class="xdb-item exchange" open-type="getPhoneNumber" @getphonenumber="exchangeBefore">马上兑奖</button>
					  	<cover-image class="xdb-item-bg" src="../../../static/images/dialog_btn_bg01.png"></cover-image>
					  </cover-view>
				  </cover-view>
				  
		</cover-view>
		<!-- 关闭按钮 -->
		<cover-image class="xh-dialog-close fadeInOpacity" :style="{display: isShow?'block':'none'}" src="../static/images/toast_close.png" @click="close"/>
		<!-- 黑幕 -->
		<cover-view class="xh-dialog-black"  @touchmove.stop></cover-view>
	</cover-view>

</template>

<script>
	import {
		mapGetters,
		mapMutations,
		mapActions
	} from 'vuex'

	export default {
		data() {
			return {
				parmas: null,//劵参数
				isShow:false,//显示隐藏控制
				type:'26'//25 26 代表对应活动类型 25周年 26周年 
			}
		},
		computed: {
			...mapGetters(['userInfo'])
		},
		methods: {
			...mapMutations({
				setCheckCardVolume:'personal/setCheckCardVolume'
			}),
			...mapActions({
				updateUserMobile:'login/updateUserMobile',
				getUserInfo:'login/getUserInfo',
				getCardTopCount:'personal/getCardTopCount',
				wxlogin:'login/wxlogin'
			}),
			show(data) {
				this.parmas = data
				this.isShow = true
				//中奖了更新顶部信息
				this.getCardTopCount()
			},
			close() {
				this.isShow = false
				this.$emit('closeNotice')
			},
			goCardBag() {
				this.$router.redirectTo({
					url: '/pages/personal/myCardBag/index'
				})
				this.isShow = false
			},
			exchangeBefore(e) {
				
				this.wxlogin().then(()=>{
					
					this.updateUserMobile(e).then(res => {
						//开始兑奖
						this.exchange()
						//异步更新用户信息
						this.getUserInfo(true)
						
					})
					
				})
				
			},
			exchange() {

				this.setCheckCardVolume([this.parmas]) //当前兑奖卷
				this.$router.redirectTo({
					url: '/pages/personal/storesCode/index'
				})
				this.isShow = false

			}
		}
	}
</script>

<style lang="scss">
	.xh-winning-window {
		
       .dialog-tips-img{
		   width: 294rpx;
		   height: 80rpx;
		   position: absolute;
		   top: 298rpx;
		   left: 50%;
		   transform: translateX(-50%);
	   }
	   .card-coupons{
		   width: 554rpx;
		   height: 148rpx;
		   position: absolute;
		   top: 404rpx;
		   left: 50%;
		   transform: translateX(-50%);
		   display: flex;
		   align-items: center;
		   border-radius: 5px;
		   overflow: hidden;
		   margin-left: 8rpx;
	   }
	   .cc-left-icon{
		  width: 148rpx;
		  height: 148rpx;
	   }
	   .cc-right-bg{
		   position: absolute;
		   z-index: -1;
		   left: 148rpx;
		   width: auto;
		   right: 0;
		   top: 0;
	   }
	   .cc-right-info{
		   flex: 1;
		   margin-left: 30rpx;
	   }
	   .cc-r-i-title{
		   font-size: 30rpx;
		   color: #333;
		   font-weight: bold;
		   margin-bottom: 15rpx;
	   }
	   .cc-r-i-time{
		   font-size: 22rpx;
		   color: #999;
	   }
	   .deposit{
		   color: #F5231F;
	   }
	   .exchange{
		   color: #614900;
	   }
	   .xh-dialog-close{
		   bottom: 15%;
	   }
	}
</style>
